/* global FB */
export const share2FB = (title, image, callback) => {
  const options = {
    method: 'share_open_graph',
    action_type: 'og.likes',
    action_properties: JSON.stringify({
      object: {
        'og:url': 'https://program-helper.github.io',
        'og:title': title,
        'og:description': '算算再修幾學分可以換一張學程證書！點擊查看更多！',
        'og:image': image,
      }
    })
  };
  FB.ui(options, callback);
}