<template>
  <section class="py-3 blue-grey lighten-4">
    <v-container text-xs-center style="max-width: 750px">
      <v-btn style="color: #3b5998;" outline :loading="loading" @click="share">
        <img src="../assets/fb.png" alt="" height="14" class="mr-2">
        分享至 Facebook
      </v-btn>
      <div :style="{ maxWidth: '720px', minWidth: '480px', position: 'relative', left: canvasLeft }">
        <canvas ref="radar"/>
      </div>
      <div style="width: 720px; position: absolute; visibility: hidden;">
        <canvas ref="hiddenRadar"/>
      </div>
      <v-btn
        v-for="(m, i) in menus"
        :key="i"
        class="my-2"
        :small="$vuetify.breakpoint.xs"
        round
        :dark="activeProgram === m.name ? true : colors[i].dark"
        :color="activeProgram === m.name ? 'secondary' : colors[i].class"
        @click="active(m.name)">{{ `${m.name} (${m.doneCredit}/${m.minCredit})` }}</v-btn>
    </v-container>
  </section>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';
import { SET_ACTIVE_PROGRAM, LOG_COURSE_LOG } from '../store/constants';
import { PROGRAMS } from '../functions/Program';
import { uploadImage } from '../functions/uploadImage';
import { share2FB } from '../functions/share2FB';
import Chart from 'chart.js';

const colors = [
  { class: 'blue darken-4', dark: true },
  { class: 'blue darken-3', dark: true },
  { class: 'blue darken-2', dark: true },
  { class: 'blue darken-1', dark: true },
  { class: 'blue lighten-1', dark: false },
  { class: 'blue lighten-2', dark: false },
  { class: 'blue lighten-3', dark: false },
  { class: 'blue lighten-4', dark: false },
  { class: 'blue lighten-5', dark: false },
  { class: 'blue lighten-5', dark: false },
  { class: 'blue lighten-5', dark: false },
  { class: 'blue lighten-5', dark: false },
];

const radarBase = {
  type: 'radar',
  data: {
    labels: PROGRAMS.map(v => v.name),
    datasets: [{
      backgroundColor: '#2196F3',
      borderColor: '#2196F3',
    }],
  },
  options: {
    legend: {
      display: false,
      label: {
        fontColor: 'blue'
      },
    },
    scale: {
      ticks: {
        display: false,
        max: 100,
        min: 0,
        stepSize: 20
      },
      pointLabels: {
        fontSize: 14,
        fontColor: 'rgba(0, 0, 0, .6)',
        fontFamily: "'Roboto', 'sans-serif'",
      }
    },
    layout: {
      padding: {
        top: 24,
        bottom: 36,
      }
    },
    animation: {
      duration: 0,
    },
  },
};

export default {
  name: 'program-menu',
  data() {
    return {
      colors,
      loading: false,
      canvasLeft: 0,
      isMounted: false,
    }
  },
  computed: {
    ...mapGetters(['programs', 'menus', 'activeProgram']),
    dataset() {
      return PROGRAMS.map(v => this.programs[v.name]).map(v => Math.min(Math.round(v.doneCredit / v.minCredit * 100), 100));
    },
  },
  watch: {
    programs() {
      if (this.isMounted) this.chart();
    }
  },
  methods: {
    ...mapActions([SET_ACTIVE_PROGRAM, LOG_COURSE_LOG]),
    chart() {
      const option1 = JSON.parse(JSON.stringify(radarBase));
      option1.data.datasets[0].data = this.dataset;
      option1.options.animation.onComplete = () => setTimeout(() => {
        if (this.updated()) this.upload();
      }, 100);
      new Chart(this.$refs.hiddenRadar, option1);

      const option2 = JSON.parse(JSON.stringify(radarBase));
      option2.data.datasets[0].data = this.dataset;
      option2.options.scale.pointLabels.fontSize = this.$vuetify.breakpoint.xs ? 12 : 14;
      new Chart(this.$refs.radar, option2);      
    },
    updated() {
      const stored = localStorage.getItem('dataset')
      const current = this.dataset.toString();
      const zero = new Array(12).fill(0).toString();

      if (stored === null || current === zero) {
        localStorage.setItem('dataset', zero);
        localStorage.setItem('imageUrl', 'https://i.imgur.com/AYHwYSV.png');
      }
      return current !== zero && current !== stored;
    },
    async upload() {
      this.loading = true;
      try {
        const dataUrl = this.$refs.hiddenRadar.toDataURL('image/png');
        const imageUrl = await uploadImage(dataUrl);
        console.log(imageUrl);
        localStorage.setItem('imageUrl', imageUrl);
        localStorage.setItem('dataset', this.dataset.toString());
      } catch (error) {
        console.log('error');
      } finally {
        this.loading = false;
      }
    },
    share() {
      this.loading = true;
      const imageUrl = localStorage.getItem('imageUrl');
      const text = this.menus[0].doneCredit > 0 ? `我最適合的清大學程是：${this.menus[0].name}` : '清大學程小幫手';
      share2FB(text, imageUrl, res => this.loading = false);
    },
    active(name) {
      this[SET_ACTIVE_PROGRAM](name);
      this.$vuetify.goTo('#program-info');
    },
  },
  mounted() {
    this.isMounted = true;
    this.chart();
    this.canvasLeft = `calc((100% - ${this.$refs.radar.clientWidth}px) / 2)`;
  },
}
</script>
