const timeMapping = {
  '10': '上',
  '20': '下',
  '30': '暑',
};

export default {
  name: 'program-list-item',
  props: {
    item: Object,
    settings: Array,
  },
  computed: {
    creditSemesters() {
      const { credits, records } = this.item;
      const output = Array.from(new Set(credits)).reduce((acc, v) => {
        acc[v] = new Set();
        return acc;
      }, {});
      credits.forEach((v, i) => output[v].add(this.record2time(records[i])));
      return Object.keys(output).map(v => `${v} 學分：${Array.from(output[v]).join('、')}`).join('<br>');
    },
    ordinalYear(){
      const { records } = this.item;
      const output = Array.from(new Set(records.map(v => v.slice(3, 5)))).reduce((acc, v) => {
        acc[v] = new Set();
        return acc;
      }, {});
      records.forEach(v => output[v.slice(3, 5)].add(v.slice(0, 3)));
      return Object.keys(output).map(v => `${timeMapping[v]}：${Array.from(output[v]).join('、')}`).join('<br>');
    },
    depts() {
      const { records } = this.item;
      return Array.from(new Set(records.map(v => v.slice(5, 9).trim()))).map(v => codeMapping[v]).join('<br>');
    }
  },
  methods: {
    record2time(r) {
      return `${r.slice(0, 3)} ${timeMapping[r.slice(3, 5)]}`;
    },
    record2dept(r) {
      const code = r.slice(5, 9).trim();
      if (!codeMapping[code]) console.log(code);
      return codeMapping[code] || code;
    },
  },
}
