const MAX_INT = Number.MAX_SAFE_INTEGER;

export class Node {
  constructor(children, validation = {}, description = '') {
    this.children = children;
    this.validation = {
      min_children: 0,
      max_children: MAX_INT,
      min_credit: 0,
      min_valid_children_node: MAX_INT,
      ...validation,
    };
    this.description = description;

    this.credit = 0;
    this.valid = false;
    this.selected = false;
  }

  select() {
    this.selected = true;
  }

  unselect() {
    this.selected = false;
  }

  validate() {
    const {
      min_children,
      max_children,
      min_credit,
      min_valid_children_node,
    } = this.validation, { children } = this;

    children.forEach(v => v.validate());

    const selected_children = children
      .map((v, i) => i)
      .filter(v => children[v].valid && children[v].credit > 0)
      .sort((a, b) => children[b].credit - children[a].credit)
      .slice(0, max_children)
      .map(v => children[v]);
    let credit = 0;
    selected_children.forEach(c => {
      c.select();
      credit += c.credit;
    });
    this.credit = credit;

    const children_nodes = children.filter(v => v instanceof Node);
    const valid_children_node = children_nodes.filter(v => v.valid);
    this.valid =
      valid_children_node.length >= Math.min(min_valid_children_node, children_nodes.length) &&
      selected_children.length >= min_children &&
      credit >= min_credit;

    this.unselect();
  }
}

class Course {
  constructor({ name, records, credits, enrolled_course_indices }) {
    this.name = name;
    this.records = records;
    this.credits = credits;

    this.enrolled = enrolled_course_indices.length > 0;
    this.record = records[enrolled_course_indices[0]] || null;
    this.credit = credits[enrolled_course_indices[0]] || 0;

    this.validation = {
      related_courses: [],
      truth_table: [],
    }

    this.selectable = false;
    this.valid = false;
    this.selected = false;
  }

  relate(related_courses, truth_table) {
    this.validation.related_courses = related_courses;
    this.validation.truth_table = truth_table;
  }

  select() {
    this.selected = true;
  }

  unselect() {
    this.selected = false;
  }

  validate() {
    const { related_courses, truth_table } = this.validation;
    this.selectable = related_courses.length === 0 ? true : truth_table[related_courses.reverse().map(v => v.enrolled).reduce((acc, cur, idx) => acc + (cur << idx), 0)];
    this.valid = this.enrolled && this.selectable;
    this.unselect();
  }
}

